import React, { useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import styled from 'styled-components'
import { ButtonRound, ErrorText } from '../stylesheets/mainStyled'
import { ReactComponent as CloseIcon } from '../images/close.svg'
import { ReactComponent as CheckIcon } from '../images/check.svg'
import { device } from '../stylesheets/device'
import TextInput from './FormikInputs/TextInput'
import { getVehiche } from '../redux/vehicles/vehiclesOperations'
import {
  getVehicleId,
  getVehiclesError,
  getVehiclesLoader,
} from '../redux/vehicles/vehiclesSelectors'
import Loading from '../components/Loader'
import { editVehicle } from '../services/apiMethods'
import { getAuth } from '../redux/global/globalSelectors'
import {
  editVehicleId,
  vehiclesError,
  vehiclesLoader,
} from '../redux/vehicles/vehiclesActions'

const EditVehicle = ({ onClose, id }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)
  const vehicle = useSelector(getVehicleId)
  const loader = useSelector(getVehiclesLoader)
  const error = useSelector(getVehiclesError)

  const getVehicleInfo = useCallback(id => dispatch(getVehiche(id)), [dispatch])
  useEffect(() => {
    getVehicleInfo(id)
  }, [getVehicleInfo, id])
  return (
    <Container>
      <Header>
        <Title>Автобус</Title>
        <CloseIcon onClick={onClose} />
      </Header>
      {vehicle && (
        <Formik
          initialValues={{ ...vehicle }}
          validationSchema={Yup.object({
            model: Yup.string().required('is required'),
            number: Yup.string().required('is required'),
            capacity: Yup.number().required('is required'),
          })}
          onSubmit={async ({ model, number, capacity }) => {
            const vehicle = { id, model, number, capacity }
            dispatch(vehiclesLoader())
            try {
              const { data } = await editVehicle(vehicle, auth)
              dispatch(editVehicleId(data))
              onClose()
            } catch (error) {
              dispatch(vehiclesError(error))
            }
          }}
        >
          <Form>
            <FormBox>
              <TextInput label="Название" name="model" type="text" />
              <TextInput label="Госномер" name="number" type="text" />
              <TextInput label="Вместительность" name="capacity" type="text" />
              {error && <ErrorText>error</ErrorText>}
              <ButtonRound type="submit">
                {loader ? (
                  <Loading type="ThreeDots" color="#00BFFF" height={15} width={40} />
                ) : (
                  <CheckIcon />
                )}
              </ButtonRound>
            </FormBox>
          </Form>
        </Formik>
      )}
    </Container>
  )
}

export default EditVehicle

const Container = styled.div`
  background-color: #fff;
  width: 100%;
  height: 100%;
  position: relative;

  @media ${device.tablet} {
    width: 320px;
    margin: 0 auto;
  }
`
const Header = styled.div`
  padding: 12.4px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
`
const Title = styled.p`
  font-size: 24px;
`
const FormBox = styled.div`
  padding: 20px;
`
