import React, { useState, useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components'
import '../stylesheets/animation.css'
import { ReactComponent as DeleteIcon } from '../images/delete.svg'
import ButtonAdd from '../components/ButtonAdd'
import { device } from '../stylesheets/device'
import AddVehicle from '../components/AddVehicle'
import EditVehicle from '../components/EditVehicle'
import ConfirmRemove from '../components/ModalWindows/ConfirmRemove'
import Modal from '../components/Modal'
import { getAllVehiches } from '../redux/vehicles/vehiclesOperations'
import { getVehiclesAll } from '../redux/vehicles/vehiclesSelectors'
import {
  removeVehicleId,
  vehicleId,
  vehiclesError,
} from '../redux/vehicles/vehiclesActions'
import { removeVehicle } from '../services/apiMethods'
import { getAuth } from '../redux/global/globalSelectors'

const VehiclesPage = () => {
  const dispatch = useDispatch()
  const vehicles = useSelector(getVehiclesAll)
  const [isAdd, setIsAdd] = useState(false)

  const getVehicles = useCallback(() => dispatch(getAllVehiches()), [dispatch])
  useEffect(() => getVehicles(), [getVehicles])

  const handleCloseModalAdd = () => {
    setIsAdd(false)
    dispatch(vehiclesError(null))
  }
  const handleOpenModalAdd = () => setIsAdd(true)

  return (
    <>
      <Container>
        {vehicles.length > 0 ? (
          vehicles.map(el => <Vehicle key={el.id} bus={el} />)
        ) : (
          <Text>Добавте свой первый автобус</Text>
        )}
      </Container>
      <ButtonAdd click={handleOpenModalAdd} />
      <Modal open={isAdd} onClose={handleCloseModalAdd}>
        <AddVehicle onClose={handleCloseModalAdd} />
      </Modal>
    </>
  )
}

export default VehiclesPage

const Vehicle = ({ bus }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)
  const [isEdit, setIsEdit] = useState(false)
  const [isRemove, setIsRemove] = useState(false)

  const handleOpenEdit = () => {
    setIsEdit(true)
  }
  const handleCloseEdit = () => {
    setIsEdit(false)
    dispatch(vehicleId(null))
    dispatch(vehiclesError(null))
  }

  const toggleOpenModalRemove = () => setIsRemove(!isRemove)
  const handleConfirmRemove = async () => {
    try {
      const { status } = await removeVehicle(bus.id, auth)
      if (status === 200) {
        setIsRemove(false)
        dispatch(removeVehicleId(bus.id))
      }
    } catch (error) {
      dispatch(vehiclesError(error))
    }
  }
  return (
    <>
      <Card>
        <FlexColumn onClick={handleOpenEdit}>
          <BusNumber>{bus.number}</BusNumber>
          <Text>{bus.model}</Text>
          <Text>
            Мест всего:<span> {bus.capacity}</span>
          </Text>
        </FlexColumn>
        <DeleteIcon onClick={toggleOpenModalRemove} />
      </Card>
      {/* modal edit */}
      <Modal open={isEdit} onClose={handleCloseEdit}>
        <EditVehicle onClose={handleCloseEdit} id={bus.id} />
      </Modal>
      {/* modal remove */}
      <Modal open={isRemove} onClose={toggleOpenModalRemove}>
        <ConfirmRemove
          name={`${bus.model} ${bus.number}`}
          onConfirm={handleConfirmRemove}
          onCancel={toggleOpenModalRemove}
        />
      </Modal>
    </>
  )
}

const Container = styled.div`
  padding: 20px;
  @media ${device.tablet} {
    display: flex;
    flex-wrap: wrap;
  }
`
const Card = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px 20px 0;
  margin-bottom: 20px;
  border-bottom: ${prop => prop.theme.border};
  @media ${device.tablet} {
    flex-direction: column;
    border: ${prop => prop.theme.border};
    border-radius: 4px;
    padding: 12px;
    background-color: rgba(0, 0, 0, 0.1);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
    margin: 0 20px 20px 0;
  }
`

const FlexColumn = styled.div`
  display: flex;
  flex-direction: column;
  cursor: pointer;
  @media ${device.tablet} {
    margin-bottom: 12px;
  }
`
const BusNumber = styled.p`
  padding: 6px 20px;
  margin-bottom: 12px;
  border: ${prop => prop.theme.border};
  border-radius: 4px;
  font-size: 18px;
  background-color: #fff;
`
const Text = styled.p`
  font-size: 18px;
  @media ${device.tablet} {
    margin-bottom: 6px;
    &:last-child {
      margin: 0;
    }
  }
`
