import axios from 'axios'
import * as globalSelectors from '../global/globalSelectors'
import { vehicleId, vehiclesAll, vehiclesError, vehiclesLoader } from './vehiclesActions'

axios.defaults.baseURL = 'https://car.busis.eu'

export const getAllVehiches = () => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  dispatch(vehiclesLoader())
  try {
    const { data } = await axios('/api/vehicle/all', {
      headers: { Authorization: auth },
    })
    dispatch(vehiclesAll(data))
  } catch (error) {
    dispatch(vehiclesError(error))
  }
}

export const getVehiche = id => async (dispatch, getState) => {
  const auth = globalSelectors.getAuth(getState())
  if (!auth) return
  dispatch(vehiclesLoader())
  try {
    const { data } = await axios(`/api/vehicle?vehicleId=${id}`, {
      headers: { Authorization: auth },
    })
    dispatch(vehicleId(data))
  } catch (error) {
    dispatch(vehiclesError(error))
  }
}
